.main {
    text-align: center;
}

.about-skills-content {
    padding-top: 2.5%;
}

.skills-buttons {
    margin-left: auto;
    margin-right: auto;
    width: 100%;
}

.about-text {
    margin-left: auto;
    margin-right: auto;
    width: 90%;
    font-family: 'Exo 2', sans-serif;
}

.certificates-content {
    display: flex;
    flex-direction: column;
    width: 90%;
    margin-left: auto;
    margin-right: auto;
}

.first-row {
    justify-content: center;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
}

.second-row {
    display: flex;
    margin-top: 1%;
    margin-bottom: 1%
}

/* Large Screens */
@media only screen and (min-width: 600px) {
    .about-skills-content {
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
    }

    .about-content {
        width: 50%;
    }
    .skills-content {
        width: 50%;
    }

    .second-row {
        flex-direction: row;
        justify-content: space-evenly;
        margin-bottom: 4%;
    }

    .fso {
        max-width: 40%;
        padding-right: 4%;
        padding-left: 4%
    }

    .cs50 {
        max-width: 75%;
    }

}

/* Small Screens */
@media only screen and (max-width: 600px) {
    .first-row {
        padding-bottom: 2%;
    }

    .fso {
        width: 75%;
        padding-bottom: 2.5%;
    }

    .cs50 {
        width: 75%;
    }

    .second-row {
        flex-direction: column;
    }

    .second-row > div {
        padding-bottom: 2.5%;
    }
}