body {
    background-color: #191919;
    font-family: 'Open Sans', sans-serif;
}
.splash {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    display: flex;
    overflow: hidden;
}
.splash_logo {
    position: absolute;
    margin: -15px 0 0 -25px;
    top: 50vh;
    z-index: 5;
    left: 50vw;
    width: 50px;
    text-align: center;
    height: 30px;
    font-size: 26px;
    font-weight: 600;
    color: #fff;
    opacity: 1;
    will-change: opacity;
    animation: logo .3s ease-in 1.5s forwards, off .6s ease-in-out 3.2s forwards;
}
.splash_logo:before {
    display: block;
    position: absolute;
    left: 15px;
    bottom: -5px;
    width: 20px;
    height: 1px;
    background-color: #757474;
    content: "";
}
.splash_logo:after {
    display: block;
    position: absolute;
    left: 15px;
    top: -5px;
    width: 20px;
    height: 1px;
    background-color: #757474;
    content: "";
    will-change: width;
}
.splash_svg {
    position: relative;
    margin: auto;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    bottom: 0;
    right: 0;
}
.splash_svg svg {
    width: 100%;
    height: 100%;
    backface-visibility: visible;
}
.splash_svg svg rect {
    width: 100%;
    height: 100%;
    fill: rgb(35, 0, 0);
    stroke: 0;
    clip-path: polygon(45vw 40vh, 55vw 40vh, 55vw 60vh, 45vw 60vh);
    animation: expand .7s ease-in forwards 2.7s;
}
.splash_minimize {
    position: absolute;
    margin: auto;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 4;
}
.splash_minimize svg {
    width: 100%;
    height: 100%;
    backface-visibility: visible;
}
.splash_minimize svg rect {
    width: 100%;
    height: 100%;
    clip-path: polygon(0vw 0vh, 100vw 0vh, 100vw 100vh, 0vw 100vh);
    animation: scale .2s ease-out forwards 1s, hide 1.3s ease-out forwards 1.2s;
}

@keyframes scale {
    100% {
        clip-path: polygon(45vw 40vh, 55vw 40vh, 55vw 60vh, 45vw 60vh);
   }
}

@keyframes hide {
    100% {
        fill: transparent;
   }
}

@keyframes off {
    100% {
        opacity: 0;
   }
}

@keyframes on {
    100% {
        opacity: 1;
   }
}

@keyframes logo {
    100% {
        color: black;
   }
}

@keyframes type {
    0% {
        width: 0;
   }
}
@keyframes expand {
    25% {
        clip-path: polygon(0vw 0vh, 55vw 40vh, 55vw 60vh, 45vw 60vh);
        fill: rgb(35, 0, 0);
   }
    50% {
        clip-path: polygon(0vw 0vh, 100vw 0vh, 55vw 60vh, 45vw 60vh);
        fill: rgb(35, 0, 0);
   }
    75% {
        clip-path: polygon(0vw 0vh, 100vw 0vh, 55vw 60vh, 0vw 100vh);
        fill: rgb(35, 0, 0);
   }
    100% {
        clip-path: polygon(0vw 0vh, 100vw 0vh, 100vw 100vh, 0vw 100vh);
        fill: black;
   }
}
