@media only screen and (min-width: 830px) {
    .gradient-border {
        border-style: solid;
        border-left: none;
        border-right: none;
    }
    .container {
        display: flex;
        flex-direction: column;
    }
    .title-content {
        margin-bottom: 2.5%;
        text-align: start;
    }
    .main-content {
        display: flex;
        flex-direction: row;
        text-align: start;
    }
    .image-content {
        width: 45%;
        margin-right: 5%;
        margin-bottom: 5%;
    }
    .image-content img {
        width: 100%;
        object-fit: contain;
        object-position: center;
        overflow: hidden;
    }
    .text-content {
        display: flex;
        flex-direction: column;
        width: 50%;
    }
    .links-content {
        width: 100%;
        height: 45%;
    }
    .description-content {
        width: 100%;
        min-height: 45%;
    }
    .header {
        margin-top: 0;
    }
}


@media only screen and (max-width: 830px) {
    .main {
        padding-bottom: 25%;
    }
    .container {
        display: flex;
        flex-direction: column;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
    }
    .title-content {
        margin-bottom: 2.5%;
    }
    .main-content {
        display: flex;
        flex-direction: column;
    }
    .image-content {
        width: 100%;
    }
    .image-content img {
        width: 100%;
        object-fit: contain;
        object-position: center;
        overflow: hidden;
    }
    .text-content {
        display: flex;
        flex-direction: column;
    }
    .links-content {
        width: 100%;
        height: 45%;
    }
    .description-content {
        width: 100%;
        height: 45%;
    }
}

.container {
    min-height: fit-content;
    margin: 0.25%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 2.5%;
    padding: 2.4%;
    background: none;
}

.gradient-border {
    --borderWidth: 1.5px;
    background: #202022;
    position: relative;
    border-radius: 25%;
  }
  .gradient-border:after {
    content: '';
    position: absolute;
    top: calc(-1 * var(--borderWidth));
    left: calc(-1 * var(--borderWidth));
    height: calc(100% + var(--borderWidth) * 2);
    width: calc(100% + var(--borderWidth) * 2);
    background: linear-gradient(60deg, #000000, #370000, #520000, #760000, #810000, #a50000, #cf0000, #ff0000);
    border-radius: calc(2 * var(--borderWidth));
    z-index: -1;
    animation: animatedgradient 3s ease alternate infinite;
    background-size: 300% 300%;
  }
  
  
  @keyframes animatedgradient {
      0% {
          background-position: 0% 50%;
      }
      50% {
          background-position: 100% 50%;
      }
      100% {
          background-position: 0% 50%;
      }
  }